const fetchData = async (location, days) => {
    try {
        const response = await fetch(`https://api.weatherapi.com/v1/forecast.json?key=bec0a55c5686475981d152146211810&q=${location}&days=${days}&aqi=no&alerts=no`);
        const data = await response.json();
        return data;
    } catch (e) {
        console.error(e);
    }
};

export default fetchData;